import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

import all from '../store/components/all.js';
import city_store from '../store/components/city_store.js';


export default new Vuex.Store({
    modules: {
        all: all,
        city: city_store
    }
})


