<template>

  <div class="h_agentList" id="h_agentList">
    <div class="head">
      <mt-header :title="titleS" fixed>
        <mt-button slot="left" @click="goback()">
          <img class="backImg" src="../../assets/images/nav_menu_back.png" alt="">
        </mt-button>
      </mt-header>
    </div>
    <div style="margin: 54px 3% 0 3%;">
      <input type="text" class="searchIn" v-model="searchdata" placeholder="搜索项目名称" @click="goSearch('/search_apply')" readonly="readonly" onfocus="this.blur();"/>
      <!-- <input type="button" class="searchBtn" value="搜索" /> -->
    </div>
    <div class="page-loadmore">
      <div style="width: 100%; text-align: center; color: red; font-size: 18px; margin-top:18px;" v-show="nullData">暂无数据</div>
      <div class="page-loadmore-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <mt-loadmore :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore">
          <div class="list" v-for="(item, index) in listData" :key="index">
            <h3 class="Htitle">{{item.project_name}}</h3>
            <img v-if="item.stateD == '0'" onclick="return false" class="imgS" src="../../assets/images/apply-0.png" alt="">
            <img v-if="item.stateD == '1'" onclick="return false" class="imgS" src="../../assets/images/apply-1.png" alt="">
            <img v-if="item.stateD == '2'" onclick="return false" class="imgS" src="../../assets/images/apply-2.png" alt="">
            <img v-if="item.stateD == '3'" onclick="return false" class="imgS" src="../../assets/images/apply-3.png" alt="">
            <img v-if="item.stateD == '4'" onclick="return false" class="imgS" src="../../assets/images/apply-4.png" alt="">
            <img v-if="item.stateD == '5'" onclick="return false" class="imgS" src="../../assets/images/apply-5.png" alt="">
            <a class="linker" target="_blank" :href="item.journey_url"></a>
          </div>
          <div slot="bottom" class="mint-loadmore-bottom" v-show="listData.length > 9">
            <span v-show="bottomStatus !== 'loading'" :class="{ 'is-rotate': bottomStatus === 'drop' }">{{isEndShow}}</span>
            <span v-show="bottomStatus === 'loading'">
              <mt-spinner type="snake"></mt-spinner>
            </span>
          </div>
        </mt-loadmore>
      </div>
    </div>

    <mt-tabbar v-model="selected" fixed v-show="titleS == '报建进度' || titleS == '施工进度'">
      <mt-tab-item id="apply_progress">
        报建进度
      </mt-tab-item>
      <mt-tab-item id="construction_schedule">
        施工进度
      </mt-tab-item>
    </mt-tabbar>

  </div>
</template>

<script type="text/babel">
import { MessageBox, Toast, Search, Loadmore } from "mint-ui";
export default {
  data() {
    return {
      nullData: false,
      alltotalPage: 0,
      isEndShow: '↑',
      titleS: '报建进度',
      selected: 'apply_progress',
      searchdata: '',
      listData: [],
      page: 0,
      list: '',
      allLoaded: false,
      bottomStatus: "",
      wrapperHeight: 537
    };
  },
  methods: {
    /* 跳转到详情 */
     edital(journey_url){
       window.location = journey_url
      // this.listData.forEach(element => {
      //   if(id == element.id){
      //     this.$store.state.all.alertData = [];
      //     this.$store.state.all.alertData.push(element);
      //     this.$router.push({path: "/applyEdital"});
      //   }
      // })
    },
    goSearch(da){
      this.$router.push({path: da});
    },
        // 查询数据
     getAllSearchDatefun() {
      this.page = 0;
      this.listData = [];
      this. isEndShow = '↑';
      this.allLoaded = false;
      let params = { params: {flow_type_id: '146', project_name: this.searchdata ,page: 1} };
      let that = this;
      this.$root.contains(that,params)
    },
     goback(data) {
      Toast({
        message: "该出去了",
        position: "top",
        duration: 5000
      });
    },
    handleBottomChange(status) {
      console.log(status)
      this.bottomStatus = status;
    },
    loadBottom() {
      console.log("loadBottom")
    
      setTimeout(() => {
        // console.log(this.listData.length)
        // console.log(this.alltotalPage)
        if (this.listData.length < this.alltotalPage) {
          this.page += 1;
          if(this.page > 1){
          let params = { params: {flow_type_id: '146', project_name: '', page: this.page} };
          let that = this;
          this.$root.contains(that,params);
          }
        } else {
          this.isEndShow = '没有更多内容',
          this.allLoaded = true;
        }
             this.$refs.loadmore.onBottomLoaded();
      },500);
      if(this.page < 1){
          setTimeout(function () {
                    document.querySelector('.page-loadmore-wrapper').scrollTop = 0;
          }, 500)
      }
    
    }
  },
  created() {
     let params = { params: {flow_type_id: '146', project_name: this.searchdata, page: 1} };
    let that = this;
    this.$root.contains(this,params);
  },
  mounted() {
   
    // this.getAllDatefun();
    // this.wrapperHeight =
    //   document.documentElement.clientHeight -
    //   this.$refs.wrapper.getBoundingClientRect().top;


    
  },
       //监听路由的路径，可以通过不同的路径去选择不同的切换效果
  watch: {
     selected: function (val, oldVal) {
      if(val == 'construction_schedule'){
        this.$router.push({path: "/construction"});
      }
      else if(val == 'apply_progress'){
        this.$router.push({path: "/"});
      }
    }
  },
};
</script>

<style scoped>

.h_agentList {
  display: block;
  overflow: auto;
  width: 100%;
  background-color: rgba(221,239,255,0.1);
}
.page-loadmore .list{
  display: block;
  width: 94%;
  margin: 10px 3%;
  overflow: hidden;
  height: 96px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0px 0px 12px rgba(221,239,255,1);
  position: relative;
}
.page-loadmore .list .linker{
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 3;
}

.page-loadmore .list .Htitle{
  width: 100%;
  font-size: 16px;
  font-weight: bold;
  padding: 13px;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}
.page-loadmore .list .imgS{
  padding: 0 13px 20px 13px;
  width: 100%;
  overflow: hidden;
}

.searchIn {
  -webkit-appearance: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 30px;
  line-height: 30px;
  outline: 0;
  padding: 0 5px;
  width: 100%;
  outline: none;
}
.searchBtn {
  -webkit-appearance: none;
  font-size: 14px!important;
  border: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #333;
  display: inline-block;
  font-size: inherit;
  height: 30px;
  line-height: 30px;
  outline: 0;
  padding: 0 5px;
  width: 11%;
  outline: none;
  background: none;
}

.page-loadmore {
  width: 100%;
  overflow-x: hidden;
  margin-top: 4px;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
  padding-bottom: 44px;
}
.page-loadmore .mint-spinner {
  display: inline-block;
  vertical-align: middle;
}

.page-loadmore-wrapper {
  margin-top: -1px;
  overflow: scroll;
}
.page-loadmore .mint-spinner {
  display: inline-block;
  vertical-align: middle;
}
.page-loadmore-wrapper {
  overflow: scroll;
}

.mint-loadmore-bottom,
.mint-loadmore-top {
  text-align: center;
  height: 40px;
  line-height: 40px;
}
.mint-loadmore-top {
  margin-top: 0px;
}
.mint-loadmore-bottom {
  margin-bottom: 0;
}
.mint-loadmore-bottom span {
  display: inline-block;
  -webkit-transition: 0.2s linear;
  transition: 0.2s linear;
  vertical-align: middle;
}
.mint-loadmore-bottom span.is-rotate {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}
</style>



