// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import './assets/css/normal-HSY.css'
import rem from './assets/js/rem'
import MintUI from 'mint-ui'
import './assets/css/style.css'

// import VueResource from 'vue-resource'
// vuex
import store from './store'
// ajax
import axios from 'axios'
import base from './base'

Vue.config.productionTip = false

Vue.use(rem)
Vue.use(MintUI)
Vue.use(base)

axios.interceptors.request.use((config) => {
    MintUI.Indicator.open({ //打开loading
        text: '加载中...',
        spinnerType: 'fading-circle'
    });
    return config;
}, (err) => {
    MintUI.Indicator.close();
    return Promise.reject(err)
})

//响应拦截器
axios.interceptors.response.use((response) => {
    MintUI.Indicator.close(); //关闭loading
    return response;
}, (err) => {
    MintUI.Indicator.close();
    return Promise.reject(err);

})



Vue.prototype.$ajaxS = axios
    /* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    components: { App },
    template: '<App/>',
    methods: {
        // 文字转base64
        setBase(x) {
            let Base64 = require("js-base64").Base64
            return Base64.encode(x)
        },
        contains(that, par) {
            that.$ajaxS
                .get(that.$store.state.all.allUrl_for_axios + "admin/api/1/flow/flow_list", par)
                .then(
                    response => {
                        if (response.data.code == 200) {
                            let data = response.data.data.list;
                            that.alltotalPage = response.data.data.total;
                            if (data.length > 0) {
                                data.forEach(element => {
                                    element.stateD = '0'
                                    for (let i = 0; i < element.vertices.length; i++) {
                                        // console.log("==============")
                                        // console.log(element.vertices[i].vertices_id)
                                        // console.log(element.current_vertex_id)
                                        if (element.vertices[i].name == "开始节点" && element.vertices[i].vertices_id == element.current_vertex_id) {
                                            element.stateD = '0'
                                        }
                                        if (element.vertices[i].name == "初步审查" && element.vertices[i].vertices_id == element.current_vertex_id) {
                                            element.stateD = '0'
                                        }

                                        if (element.vertices[i].name == "施工图审查" && element.vertices[i].vertices_id == element.current_vertex_id) {
                                            element.stateD = '1'
                                        }
                                        if (element.vertices[i].name == "中标备案" && element.vertices[i].vertices_id == element.current_vertex_id) {
                                            element.stateD = '2'
                                        }
                                        if (element.vertices[i].name == "质量备案" && element.vertices[i].vertices_id == element.current_vertex_id) {
                                            element.stateD = '3'
                                        }
                                        if ((element.vertices[i].name == "施工许可" && element.vertices[i].vertices_id == element.current_vertex_id)) {
                                            element.stateD = '4'
                                        }
                                        if ((element.vertices[i].name == "结束节点" && element.vertices[i].vertices_id == element.current_vertex_id)) {
                                            element.stateD = '5'
                                        }


                                        for (let k = 0; k < element.reviewer_vertex_ids.length; k++) {

                                            if (element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "开始节点") {
                                                element.stateD = '0';
                                            }
                                            if (element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "初步审查") {
                                                element.stateD = '1';
                                            }
                                            if (element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "施工图审查") {
                                                element.stateD = '2';
                                            }
                                            if (element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "中标备案") {
                                                element.stateD = '3';
                                            }
                                            if (element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "质量备案") {
                                                element.stateD = '4';
                                            }
                                            if (element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "施工许可") {
                                                element.stateD = '5';
                                            }
                                        }
                                    }

                                });

                                that.list = data;
                                that.list.forEach(el => {
                                        that.listData.push(el)
                                    })
                                    // console.log(that.listData)
                            } else {
                                that.nullData = true;
                            }
                        }
                    },
                    response => {
                        MintUI.Toast({
                            message: "系统错误",
                            position: "bottom",
                            duration: 2000
                        });
                    }
                );
        }

    },

});


