import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home/home'
import homeIndex from '@/components/home/homeIndex'
import construction from '@/components/home/construction'
import search_apply from '@/components/home/search_apply'
import applyEdital from '@/components/home/applyEdital'
import constructionEdital from '@/components/home/constructionEdital'
import Error from '@/components/else/Error'

Vue.use(Router)

Router.prototype.goBack = function() {
    this.isBack = true
    window.history.go(-1)
}

export default new Router({
    // mode: 'history',
   
    routes: [
        {
            path: '/',
            component: home,
            children: [{
                    path: '',
                    component: homeIndex,
                    meta: {
                        keepAlive: true // 报建进度
                    }
                }, 
                {
                    path: '/construction', // 施工进度
                    component: construction,
                    meta: {
                        keepAlive: true // 需要被缓存
                    }
                },
                {
                    path: '/applyEdital', //  报建edital
                    component: applyEdital,
                },
                {
                    path: '/search_apply', //  报建edital
                    component: search_apply,
                },
                {
                    path: '/constructionEdital', // 施工edital
                    component: constructionEdital
                }
            ],
            meta: {
                keepAlive: true // 需要被缓存
            }
        },

        {
            path: '*',
            name: 'error',
            component: Error,
            meta: {
                keepAlive: false // 需要被缓存
            }
        }
    ]
})


