<template>
  <div class="h_agentList">
    <mt-header :title="titleS" fixed>
      <mt-button slot="left" @click="goback()">
        <img class="backImg" src="../../assets/images/nav_menu_back.png" alt="">
      </mt-button>
    </mt-header>
    <div style="margin: 10px 3%;">
      <input type="text" class="searchIn" v-model="searchdata" placeholder="输入项目名称"/>
      <input type="button" class="searchBtn" value="搜索" @click="getAllSearchDatefun"/>
    </div>
    <div class="agent">
      <div style="width: 100%; text-align: center; color: red; font-size: 18px; margin-top:18px;" v-show="listData.length < 1">暂无数据</div>
      <div class="list" v-for="(item, index) in listData" :key="index" @click="edital(item.id)">
        <h3 class="Htitle">{{item.project_name}}</h3>
        <img v-if="item.stateD == '0'" class="imgS" src="../../assets/images/apply-0.png" alt="">
        <img v-if="item.stateD == '1'" class="imgS" src="../../assets/images/apply-1.png" alt="">
        <img v-if="item.stateD == '2'" class="imgS" src="../../assets/images/apply-2.png" alt="">
        <img v-if="item.stateD == '3'" class="imgS" src="../../assets/images/apply-3.png" alt="">
        <img v-if="item.stateD == '4'" class="imgS" src="../../assets/images/apply-4.png" alt="">
        <img v-if="item.stateD == '5'" class="imgS" src="../../assets/images/apply-5.png" alt="">
      </div>
    </div>
    
    
    <mt-tabbar v-model="selected" fixed v-show="titleS == '报建进度' || titleS == '施工进度'">
      <mt-tab-item id="apply_progress">
        报建进度
      </mt-tab-item>
      <mt-tab-item id="construction_schedule">
        施工进度
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>


<script>
import Vue from 'vue'
import { MessageBox, Toast, Search, Loadmore } from "mint-ui";
export default {
  name: "construction",
  data() {
    return {
      titleS: '施工进度',
      selected: 'construction_schedule',
      isEnd: '1',
      alertData: [], //零时数据
      searchdata: '',
      listData: '',

     
    };
  },

  methods: {
      goback() {
        this.$router.goBack();
    },
        /* 获取列表数据 */
     getAllDatefun() {
      let params = { params: {flow_type_id: '146'} };
      // console.log(params);
      this.$ajaxS
        .get(this.$store.state.all.allUrl_for_axios + "admin/api/1/flow/flow_list", params)
        .then(
          response => {
            if (response.data.code == 200) {
               let data = response.data.data.list;
              if(data){
                data.forEach(element => {
                  element.stateD = '0'
                  for(let i=0; i < element.vertices.length; i++){
                    element.vertices[i].isEnd = this.isEnd;
                    if(element.vertices[i].vertices_id == element.current_vertex_id){
                     this.isEnd = '0'
                    } 
                    if(element.vertices[i].name == "开始节点" && element.vertices[i].isEnd == '1'){
                      element.stateD = '0'
                    }
                    if(element.vertices[i].name == "初步审查" && element.vertices[i].isEnd == '1'){
                      element.stateD = '1'
                    }
                 
                    if(element.vertices[i].name == "施工图审查" && element.vertices[i].isEnd == '1'){
                      element.stateD = '2'
                    }
                    if(element.vertices[i].name == "中标备案" && element.vertices[i].isEnd == '1'){
                      element.stateD = '3'
                    }
                    if(element.vertices[i].name == "质量备案" && element.vertices[i].isEnd == '1'){
                      element.stateD = '4'
                    }
                    if((element.vertices[i].name == "施工许可" && element.vertices[i].isEnd == '1')){
                      element.stateD = '5'
                    }           
                    if((element.vertices[i].name == "结束节点" && element.vertices[i].isEnd == '1')){
                      element.stateD = '5'
                    }
                    // if(this.$root(element.reviewer_vertex_ids, element.vertices[i].vertices_id)){
                      
                    // }
             
                    for(let k=0; k<element.reviewer_vertex_ids.length; k++){
                      if(element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "开始节点"){
                        element.stateD = '0';
                      }
                      if(element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "初步审查"){
                        element.stateD = '1';
                      }
                      if(element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "施工图审查"){
                        element.stateD = '2';
                      }
                      if(element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "中标备案"){
                        element.stateD = '3';
                      }
                      if(element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "质量备案"){
                        element.stateD = '4';
                      }
                      if(element.vertices[i].vertices_id == element.reviewer_vertex_ids[k] && element.vertices[i].name == "施工许可"){
                        element.stateD = '5';
                      }
                    }
                  }
          
                });
           
                this.listData = data;
                console.log(this.listData)
              }else{
                // this.listData = data;
              }
            }
          },
          response => {
            Toast({
              message: "系统错误",
              position: "bottom",
              duration: 2000
            });
          }
        );
    },

    // 查询数据
           /* 获取列表数据 */
     getAllSearchDatefun() {
      let params = { params: {flow_type_id: '146', project_name: this.searchdata} };
      // console.log(params);
      this.$ajaxS
        .get(this.$store.state.all.allUrl_for_axios + "admin/api/1/flow/flow_list", params)
        .then(
          response => {
             console.log(response)
            if (response.data.code == 200) {
               let data = response.data.data.list;
              
              if(data){
                data.forEach(element => {
                  // if(element.status == "processing"){
                  //     alert("21")
                  // }
                  element.vertices.forEach(el => {
                    if(el.name == "开始节点"){
                      element.stateD = '0'
                    }
                    if(el.name == "初步审查"){
                      element.stateD = '1'
                    }
                    if(el.name == "施工图审查"){
                      element.stateD = '2'
                    }
                    if(el.name == "中标备案"){
                      element.stateD = '3'
                    }
                    if(el.name == "质量备案"){
                      element.stateD = '4'
                    }
                    if(el.name == "施工许可"){
                      element.stateD = '5'
                    }
                   
                  })
          
                });
           
                this.listData = data;
                console.log(this.listData)
              }else{
                // this.listData = data;
              }
            }
          },
          response => {
            Toast({
              message: "系统错误",
              position: "bottom",
              duration: 2000
            });
          }
        );
    },
    edital(id){
      this.listData.forEach(element => {
        if(id == element.id){
          this.$store.state.all.alertData = [];
          this.$store.state.all.alertData.push(element);
          this.$router.push({path: "/constructionEdital"});
        }
      })
    }

    
  },
    //监听路由的路径，可以通过不同的路径去选择不同的切换效果
  watch: {
    selected: function (val, oldVal) {
      if(val == 'construction_schedule'){
        this.$router.push({path: "/construction"});
      }
      else if(val == 'apply_progress'){
        this.$router.push({path: "/"});
      }
    }
  },

  mounted(){
   // this.getAllDatefun();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
.h_agentList {
  display: block;
  overflow: auto;
  width: 100%;
  height: 100%;
  background-color: rgba(221,239,255,0.1);
}
.agent {
  width: 100%;
  margin: auto;
  overflow: hidden;
  /* min-height: 100%; */

  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
  padding-bottom: 44px;
}
.agent .list{
  width: 94%;
  margin: 10px 3%;
  overflow: hidden;
  height: 96px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0px 0px 12px rgba(221,239,255,1);
}
.agent .list .Htitle{
  width: 100%;
  font-size: 16px;
  font-weight: bold;
  padding: 13px;
  overflow: hidden;
  text-overflow:ellipsis;
  white-space: nowrap;
}
.agent .list .imgS{
  padding: 0 13px 13px 13px;
  width: 100%;
  overflow: hidden;
}
.searchIn {
  -webkit-appearance: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 30px;
  line-height: 30px;
  outline: 0;
  padding: 0 5px;
  width: 88%;
  outline: none;
}
.searchBtn {
  -webkit-appearance: none;
  font-size: 14px!important;
  border: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #333;
  display: inline-block;
  font-size: inherit;
  height: 30px;
  line-height: 30px;
  outline: 0;
  padding: 0 5px;
  width: 11%;
  outline: none;
  background: none;
}
</style>





