// 报建详情
<template>
  <div class="h_agentList">
    <div class="head">
      <mt-header :title="titleS" fixed>
        <mt-button slot="left" @click="goback()">
           <img class="backImg" src="../../assets/images/nav_menu_back.png" alt="">
        </mt-button>
      </mt-header>
    </div>
    <div style="margin-top: 54px;">
      <!-- <mt-field v-for="(item, index) in listData.field_values" :key="index" :label="item.field_name" v-model="item.text_value" :attr="{ readonly: 'true' }"></mt-field> -->
      <mt-cell v-for="(item, index) in listData.field_values" :key="index" :title="item.field_name" :value="item.text_value">
      </mt-cell>
    </div>
    <div style="margin-top:10px;">
      <!-- <mt-field v-for="(item, index) in listData.comments" :key="index" v-if="item.label" :label="item.label" v-model="item.created_at" :attr="{ readonly: 'true' }" @click="showImg(item)"></mt-field> -->
        <mt-cell is-link v-for="(item, index) in listData.comments" :key="index" v-if="item.label" :title="item.label" :label="item.created_at">
           <!-- <span style="font-size: 12px">{{}}</span> -->
          <span :class="item.status">{{item.status_alias}}</span>
          
         
          <div @click="showImg(item)" class="show-item">       
          </div>  
      </mt-cell>
    </div>
     <div class="pop" v-if="isShow">
      <div class="pop-content">
        <div class="btn" @click="closePop">×</div>
        <div class="pop-head">
          <img :src="itemDetail.user_headimgurl"><span>{{itemDetail.user_name}}</span>
        </div>
        <div class="pop-c">
          <div>电子签字：{{itemDetail.esignature?"":"暂无"}}</div>
           <img :src="itemDetail.esignature">
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import Vue from "vue";
import { MessageBox, Toast, Search, Cell } from "mint-ui";
export default {
  name: "search",
  data() {
    return {
      isShow: false,
      titleS: "报建进度详情",
      listData: "",
      itemDetail: {}
    };
  },

  methods: {
    goback() {
      this.$router.goBack();
    },
    showImg(item) {
      this.isShow = !this.isShow;
      this.itemDetail = item;
    },
    closePop() {
      this.isShow = !this.isShow;
      this.itemDetail = {};
    }
  },

  mounted() {
    this.listData = this.$store.state.all.alertData[0];
    let comments = this.listData.comments;
    let vertices = this.listData.vertices;
    for (let j = 0; j < comments.length; j++) {
      for (let i = 0; i < vertices.length; i++) {
        if (
          comments[j].vertex_id == vertices[i].vertices_id 
          // &&
          // vertices[i].name != "开始节点" &&
          // vertices[i].name != "中标单位" &&
          // vertices[i].name != "中标备案" &&
          // vertices[i].name != "结束节点"
        ) {
          this.listData.comments[j].label = vertices[i].name;
        }
      }
    }
    // console.log(this.listData)
    // this.getAllDatefun();
    // this.getAllCareDatefun();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
span.proposed {
  color: black!important
}
span.approved, span.transferred, span.finished {
  color: green!important
}
span.refused, 
span.cancelled, 
span.suspended,
span.aborted {
  color: red!important
}

.pop {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
}
.pop-content {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  background-color: #fff;
  width: 85%;
  border-radius: 3px;
  font-size: 16px;
  -webkit-user-select: none;
  padding: 15px;
  z-index: 9999;
}

.pop-content > div {
  background-color: #fff;
  color: #000;
}

.pop-head {
  height: 30px;
  line-height: 30px;
}

.btn {
  position: absolute;
  top: -12px;
  right: -16px;
  width: 30px;
  height: 30px;
  background: #ea1a1a !important;
  color: #fff !important;
  border-radius: 50%;
  text-align: center;
  line-height: 26px;
  font-size: 20px;
}

.pop-c {
  font-size: 12px;
  color: #ccc;
  margin-top: 10px;
}

.pop-head img {
  width: 30px;
  height: 30px;
  overflow: hidden;
  border-radius: 50%;
  float: left;
}

.pop-head span {
  height: 30px;
  line-height: 30px;
  float: left;
  margin-left: 10px;
}

.pop-c img {
  width: 100%;
}
.show-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.h_agentList {
  display: block;
  overflow: auto;
  width: 100%;
  height: 100%;
}
.agent {
  width: 100%;
  margin: auto;
  overflow: hidden;
  /* min-height: 100%; */
  margin-bottom: 45px;
}
.agent .list {
  width: 94%;
  margin: 10px 3%;
  overflow: hidden;
  height: 96px;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0px 0px 12px rgba(221, 239, 255, 1);
}
.agent .list .Htitle {
  width: 100%;
  font-size: 16px;
  font-weight: bold;
  padding: 13px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.agent .list .imgS {
  padding: 0 13px 13px 13px;
  width: 100%;
  overflow: hidden;
}
.searchIn {
  -webkit-appearance: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 30px;
  line-height: 30px;
  outline: 0;
  padding: 0 5px;
  width: 88%;
  outline: none;
}
.searchBtn {
  -webkit-appearance: none;
  font-size: 14px !important;
  border: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #333;
  display: inline-block;
  font-size: inherit;
  height: 30px;
  line-height: 30px;
  outline: 0;
  padding: 0 5px;
  width: 11%;
  outline: none;
  background: none;
}
</style>





