<template>
  <div class="error">
    <h1 class="titleH1">{{ msg }}</h1>
    <h3>访问的页面不存在！</h3>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      msg: '404'
    }
  }
}
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}
.titleH1 {
  color: red;
}
</style>



